@import './theme.scss';
.App {
   width:100%;
   min-height: 100vh;
   display:flex;
   flex-flow: column nowrap;
   position: relative;
   a{
      color:$blue;
      text-decoration: underline;
      
      &:hover,
      &:focus{
         text-decoration: none;
      }
   }
   header{
      width:100%;
      height:76px;
      background-color: #fff;
      box-shadow: 0 2px 5px rgba(0,0,0,.2);
      > div{
        height: inherit;
        display: flex;
        align-items: center;
        align-content: center;
        justify-content: flex-start;
        >.logo{
          width: auto;
          height: 44px;
          @media (max-width: $esMax){
            height: 30px;
          }
        }
      }
   }
   .fade-enter {
      opacity: 0;
   }
   .fade-enter-active {
      opacity: 1;
      transition: opacity .5s ease-out;
   }
   .fade-exit {
      opacity: 1;
   }
   .fade-exit-active {
      opacity: 0;
      transition: opacity .5s ease-out;
   }
}

